import {
  AppBar,
  Fab,
  Toolbar,
  Typography,
  makeStyles,
  TextField,
  Button,
} from "@material-ui/core";
import AddIcon from "@material-ui/icons/Add";
import React from "react";
import Modal from "@material-ui/core/Modal";
import Fade from "@material-ui/core/Fade";
import Backdrop from "@material-ui/core/Backdrop";
import { useEffect, useState } from "react";
import useStateRef from "react-usestateref";
import { useNavigate } from "react-router-dom";

const useStyles = makeStyles((theme) => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 200,
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
  modal: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    padding: "10px",
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    border: "2px solid #064635",
    width: "25vw",
    boxShadow: theme.shadows[5],
    // padding: theme.spacing(2, 4, 3),
  },
}));

export default function AddShopItems() {
  const classes = useStyles();
  const navigate = useNavigate();
  const [open, setOpen] = useState(false);

  const [singleItem, setSingleItem] = useState({
    iName: "",
    iPrice: "",
  });

  const [itemList, setItemList, itemListRef] = useStateRef([
    {
      iName: "None",
      iPrice: "0",
    },
  ]);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleAddItem = (e) => {

    if(singleItem.iName === "" || singleItem.iPrice === ""){
      alert("Field Cannot be empty")
    }else{

      let foundData = itemList.find(el => el.iName === singleItem.iName);

      if(foundData === undefined){
        setItemList([...itemList, singleItem]);
        setOpen(false);
    
        setSingleItem({
          iName: "",
          iPrice: "",
        });
    
        var json = JSON.stringify(itemListRef.current);
    
        console.log(json);
    
        localStorage.setItem("myList", json);
      }else{
        alert("Already Item exists")
        setSingleItem({
          iName: "",
          iPrice: "",
        });
      }

     
  
    }

    // if(itemList.length === 0 ){
    //     setItemList([...itemList , {
    //         iName: "None",
    //         iPrice: "0",
    //       }])
    //       return;
    // }
  };

  const handleName = (e) => {
    var txt = e.target.value;
    setSingleItem({ ...singleItem, iName: txt });
  };

  const handlePrice = (e) => {
    var txt = e.target.value;
    setSingleItem({ ...singleItem, iPrice: txt });
  };

  useEffect(() => {
    var locList = localStorage.getItem("myList");

    if (locList === null) {
      setItemList([]);
    } else {
      setItemList(JSON.parse(locList));
    }
  }, []);

  return (
    <div>
      <AppBar position="static">
        <Toolbar style={{ backgroundColor: "#064635" }}>
          <div style={{ width: "100%" }}>
            <Typography
              style={{
                textAlign: "center",
                fontWeight: "bold",
                fontSize: "20px",
              }}
            >
              Add Shop Items
            </Typography>
          </div>
        </Toolbar>
      </AppBar>

      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={open}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={open}>
          <div className={classes.paper}>
            <Typography
              style={{
                backgroundColor: "#064635",
                textAlign: "center",
                fontWeight: "bold",
                fontSize: "18px",
                color: "white",
                padding: "15px",
              }}
            >
              My Shop
            </Typography>
            <form className={classes.root} noValidate autoComplete="off">
              <div style={{ display: "flex", flexDirection: "column" }}>
                <TextField
                  style={{ margin: "10px" }}
                  required
                  onInput={(e) =>
                    (e.target.value = e.target.value.slice(0, 15))
                  }
                  id="outlined-required"
                  label="Item Name"
                  value={singleItem.iName}
                  onChange={(e) => handleName(e)}
                  variant="outlined"
                />
                <TextField
                  style={{ margin: "10px" }}
                  required
                  id="outlined-required"
                  type={"number"}
                  onInput={(e) => (e.target.value = e.target.value.slice(0, 5))}
                  label="Item Pirce"
                  value={singleItem.iPrice}
                  onChange={(e) => handlePrice(e)}
                  variant="outlined"
                />
                <Button
                  variant="contained"
                  style={{
                    margin: "10px",
                    color: "white",
                    backgroundColor: "#064635",
                  }}
                  onClick={(e) => handleAddItem(e)}
                >
                  Add Item
                </Button>
              </div>
            </form>
          </div>
        </Fade>
      </Modal>

      <div
        style={{
          display: "flex",
          alignItems: "center",
          flexDirection: "column",
          width: "100vw",
          marginTop: "25px",
        }}
      >
        <div
          style={{
            display: "flex",
            alignItems: "center",
            flexDirection: "column",
            width: "30vw",
            height: "75vh",
            overflow: "auto",
            backgroundColor: "#557C55",
          }}
        >
          <div
            style={{
              display: "flex",
              width: "30vw",
              justifyContent: "space-between",
              color: "white",
              backgroundColor: "#064635",
            }}
          >
            <Typography
              style={{ padding: "20px", fontWeight: "bold", fontSize: "20px" }}
            >
              Name
            </Typography>
            <Typography
              style={{ padding: "20px", fontWeight: "bold", fontSize: "20px" }}
            >
              Price
            </Typography>
          </div>

          {itemList.map((item, index) => (
            <div
              style={{
                display: "flex",
                width: "30vw",
                justifyContent: "space-between",
                color: "white",
                backgroundColor: "#557C55",
              }}
            >
              <Typography
                style={{
                  padding: "20px",
                  fontSize: "18px",
                }}
              >
                {item.iName}
              </Typography>
              <Typography
                style={{
                  padding: "20px",
                  fontSize: "18px",
                }}
              >
                {item.iPrice}
              </Typography>
            </div>
          ))}
        </div>
      </div>

      <div
        style={{
          position: "fixed",
          bottom: 25,
          width: "100vw",
          justifyContent: "center",
          alignItems: "center",
          display: "flex",
        }}
      >
        <Button
          style={{ backgroundColor: "#064635", color: "white" }}
          onClick={() => {
            navigate("/");
          }}
        >
          Back to Home
        </Button>
      </div>

      <Fab
        style={{
          position: "fixed",
          bottom: 25,
          right: 25,
          backgroundColor: "#064635",
          color: "white",
        }}
        onClick={handleOpen}
      >
        <AddIcon />
      </Fab>
    </div>
  );
}
